<template lang="pug">
	v-app
		v-app-bar(app)
			.title.theme--dark SKT 복지온 앱 버전별 다운로드
		v-main.ma-5
			v-card
				v-card-title.font-weight-bold 안드로이드
				v-card-text.envs
					v-expansion-panels(
						multiple
						v-model="opened.android"
					)
						v-expansion-panel(
							v-for="env in Object.keys(apps.android)"
						)
							v-expansion-panel-header.font-weight-bold {{ env.toUpperCase() }}
							v-expansion-panel-content
								v-list.versions
									v-list-item(
										v-for="item in apps.android[env]"
									)
										v-list-item-content
											v-list-item-title {{ item.version }}
												.buttons
													v-btn(
														color="primary"
														@click="install(item)"
													) 설치
				v-divider
				v-card-title.font-weight-bold iOS
				v-card-text.envs
					v-expansion-panels(
						multiple
						v-model="opened.ios"
					)
						v-expansion-panel(
							v-for="env in Object.keys(apps.ios)"
						)
							v-expansion-panel-header.font-weight-bold {{ env.toUpperCase() }}
							v-expansion-panel-content
								v-list.versions
									v-list-item(
										v-for="item in apps.ios[env]"
									)
										v-list-item-content
											v-list-item-title {{ item.version }}
												.buttons
													v-btn(
														color="primary"
														@click="install(item)"
													) 설치
													v-btn.ml-2(
														color="secondary"
														@click="download(item)"
													) 다운로드
</template>

<script>
	import sha1 from 'sha1'

	export default {
		name: 'App',

		data: () => ({
			opened: {
				android: [],
				ios: [],
			},
			apps: {
				android: {},
				ios: {},
			}
		}),

		async mounted() {
			const response = await
				this.axios.get(`https://welfare-skt.github.io/qa-dev/app_versions.json?t=${sha1(new Date().getMilliseconds())}`)
			this.apps = response.data

		},

		methods: {
			install(item) {
				let link = item.link.replace('&dl=0', '')
					.replace('www.dropbox.com', 'dl.dropboxusercontent.com')
					.replace('scl/fl', 's')
				if (link.includes('manifest')) {
					link = 'itms-services://?action=download-manifest&url=' + encodeURIComponent(link)
				}
				window.open(link)
			},
			download(item) {
				let link = item.file.replace('&dl=0', '')
					.replace('www.dropbox.com', 'dl.dropboxusercontent.com')
					.replace('scl/fl', 's')
				window.open(link)
			},

		}
	};
</script>

<style lang="scss">
	.envs {
		max-width: 500px;
	}
	.buttons {
		position: absolute;
		right: 0;
		margin-top: -28px;
	}
</style>